import React from "react";
import * as styles from "./Todo.module.css";

function Todo({ todo, setTodos, todos }) {
    const [editable, setEditable] = React.useState(false);
    const [done, setDone] = React.useState(todo.done);
    const input = React.useRef(null);

    React.useEffect(() => {
        if (editable) input.current.focus();
    });

    React.useEffect(() => {
        todo.done = done;
        localStorage.setItem('todos', JSON.stringify(todos));
    }, [done]); // eslint-disable-line react-hooks/exhaustive-deps

    function handleDelete() {
        setTodos(function (todos) {
            return todos.filter(function (el) {
                return el.id !== todo.id;
            });
        }
        );
    }

    function handleChange(e) {
        setTodos(todos => {
            return todos.map(item => item.id === todo.id ? { ...item, value: e.target.value } : item);
        });
    }

    function handleBlur(e) {
        setEditable(false);
    }

    function handleEdit() {
        setEditable(true);
    }

    function handleClick() {
        setDone(!done);
    }

    return (
        <li className={styles.todoList}>
            <div onClick={handleClick} tabIndex="0" className={`${styles.content} ${editable && styles.hidden} ${done && styles.done}`} role="button">{todo.value}</div>
            <input ref={input} className={`${styles.content} ${!editable && styles.hidden}`} value={todo.value} type="text" onChange={handleChange} onBlur={handleBlur} />
            <button className={styles.edit} type="button" onClick={handleEdit}></button>
            <button className={styles.del} type="button" onClick={handleDelete}></button>
        </li>
    );
}

export default Todo;